deX-leave-to {\n    transform: translateX(-100%);\n  }\n\n  .slideX-enter {\n    transform: translateX(100%);\n  }\n}\n\n.direction-prev {\n  .slideX-leave-to {\n    transform: translateX(100%);\n  }\n\n  .slideX-enter {\n    transform: translateX(-100%);\n  }\n}\n\n.slideY-enter-active,\n.slideY-leave-active {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  right: 0;\n  left: 0;\n  opacity: 1;\n  transform: translateY(0);\n  transition: all .3s ease-in-out;\n}\n\n.slideY-leave-to, .slideY-enter {\n  opacity: 0;\n}\n\n.direction-next {\n  .slideY-leave-to {\n    transform: translateY(100%);\n  }\n\n  .slideY-enter {\n    transform: translateY(-100%);\n  }\n}\n\n.direction-prev {\n  .slideY-leave-to {\n    transform: translateY(-100%);\n  }\n\n  .slideY-enter {\n    transform: translateY(100%);\n  }\n}\n\n.fade-transition {\n  opacity: 1;\n  transition: all 0.3s ease;\n}\n\n.fade-enter, .fade-leave {\n  opacity: 0;\n}\n\n.fast-updating {\n  .slideY-enter-active,\n  .slideY-leave-active {\n    transition: all 170ms ease-in-out;\n  }\n\n  .direction-next {\n    .slideY-leave-to {\n      transform: translateY(45%);\n    }\n\n    .slideY-enter {\n      transform: translateY(-5%);\n    }\n  }\n\n  .direction-prev {\n    .slideY-leave-to {\n      transform: translateY(-45%);\n    }\n\n    .slideY-enter {\n      transform: translateY(5%);\n    }\n  }\n}\n\n.#{$prefix}dir-rtl {\n  .direction-next {\n    .slideX-leave-to {\n      transform: translateX(100%);\n    }\n\n    .slideX-enter {\n      transform: translateX(-100%);\n    }\n  }\n\n  .direction-prev {\n    .slideX-leave-to {\n      transform: translateX(-100%);\n    }\n\n    .slideX-enter {\n      transform: translateX(100%);\n    }\n  }\n}\n\n.#{$prefix}content {\n  opacity: 1;\n  transition: all .4s cubic-bezier(0.25, 0.1, 0.17, 1.84);\n  text-align: right;\n  direction: rtl;\n  width: $width;\n  background-color: #ffffff;\n  box-shadow: 5px 22px 95px -14px rgba(0, 0, 0, 1);\n  cursor: default;\n}\n","$prefix: vpd-; //shorted to reduce the css size\n$width: 316px;\n\n$day-size: 40px;\n$month-label-height: 50px;\n","@use 'sass:color';\n@use 'sass:math';\n@use 'variables' as *;\n@use 'transitions';\n\n.clearfix {\n  &:after, &:before {\n    content: \" \";\n    display: table;\n  }\n\n  &:after {\n    clear: both;\n  }\n}\n\n.#{$prefix}input-group {\n  display: table;\n  width: 100%;\n  position: relative;\n\n  input {\n    display: table-cell;\n    border: 1px solid #dadada;\n    border-right: none;\n    border-top-right-radius: 0;\n    border-bottom-right-radius: 0;\n    line-height: 30px;\n    padding: 0 10px;\n\n    &:not(.is-editable) {\n      cursor: pointer;\n    }\n  }\n\n  label {\n    color: white;\n    white-space: nowrap;\n\n    svg + span {\n      display: inline-block;\n      margin-right: 4px;\n      vertical-align: middle;\n    }\n  }\n\n  &.disabled {\n    input, label {\n      cursor: default;\n    }\n  }\n\n  .#{$prefix}clear-btn {\n    position: absolute;\n    left: 0;\n    top: 0;\n    line-height: 32px;\n    width: 30px;\n    cursor: pointer;\n    text-align: center;\n    font-style: normal;\n    font-family: monospace, sans-serif;\n    opacity: 0.4;\n\n    &:hover {\n      opacity: 0.7;\n    }\n  }\n}\n\n.#{$prefix}icon-btn {\n  display: table-cell;\n  width: 1%;\n  cursor: pointer;\n  padding: 0 10px;\n  vertical-align: middle;\n\n  > svg {\n    vertical-align: middle;\n  }\n}\n\n.#{$prefix}wrapper {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  width: 100%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.5);\n  z-index: 9999;\n}\n\n.#{$prefix}container {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  user-select: none;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n}\n\n.#{$prefix}content {\n  opacity: 1;\n  transition: all .4s cubic-bezier(0.25, 0.1, 0.17, 1.84);\n  text-align: right;\n  direction: rtl;\n  width: $width;\n  background-color