}\n    }\n\n    &.selected {\n      color: #ffffff;\n\n      .#{$prefix}day-effect {\n        transform: scale(1);\n        opacity: 1;\n      }\n    }\n  }\n}\n\n.#{$prefix}day-effect {\n  position: absolute;\n  width: $day-size - 4px;\n  height: $day-size - 4px;\n  border-radius: 50%;\n  top: 2px;\n  left: 2px;\n  transform: scale(0);\n  opacity: 0;\n  transition: all 450ms ease;\n}\n\n.#{$prefix}day-text {\n  position: relative;\n}\n\n.#{$prefix}controls {\n  position: relative;\n  z-index: 2;\n  height: $month-label-height;\n  line-height: $month-label-height;\n  text-align: center;\n\n  button {\n    position: relative;\n    background-color: transparent;\n    border: none;\n    user-select: none;\n    outline: none;\n    cursor: pointer;\n\n    &[disabled=true] {\n      opacity: 0.3;\n      cursor: default;\n    }\n  }\n}\n\n.#{$prefix}next,\n.#{$prefix}prev {\n  width: $month-label-height;\n  height: $month-label-height;\n  line-height: $month-label-height;\n}\n\n.#{$prefix}next {\n  float: right;\n}\n\n.#{$prefix}prev {\n  float: left;\n}\n\n.#{$prefix}arrow {\n  width: 11px;\n  height: 11px;\n}\n\n.#{$prefix}month {\n  position: relative;\n  overflow: hidden;\n}\n\n.#{$prefix}month-label {\n  position: absolute;\n  top: 0;\n  left: $month-label-height;\n  right: $month-label-height;\n  overflow: hidden;\n  width: 95px;\n  margin-left: auto;\n  margin-right: auto;\n  line-height: $month-label-height;\n  height: $month-label-height;\n  text-align: center;\n  cursor: pointer;\n\n  > span {\n    display: inline-block;\n    padding: 0 5px;\n    line-height: 26px;\n    height: 26px;\n    border-width: 1px;\n    border-style: solid;\n    border-radius: 2px;\n    transition: all 0.1s ease-out;\n    white-space: nowrap;\n\n    &:not(:hover) {\n      border-color: transparent !important;\n      color: inherit !important;\n    }\n  }\n}\n\n.#{$prefix}actions {\n  text-align: center;\n  padding: 8px;\n\n  button {\n    border: none;\n    background-color: transparent;\n    display: inline-block;\n    cursor: pointer;\n    outline: none;\n    font-size: 14px;\n    text-transform: uppercase;\n    min-width: 88px;\n    text-align: center;\n    -webkit-appearance: none;\n    line-height: 36px;\n    height: 36px;\n    transition: all 0.3s ease;\n\n    &:hover {\n      background-color: color.adjust(#ffffff, $lightness: -5%);\n    }\n\n    &[disabled=true] {\n      opacity: 0.6;\n      cursor: default;\n    }\n  }\n}\n\n.#{$prefix}addon-list-content {\n  direction: rtl;\n}\n\n.#{$prefix}addon-list-item {\n  width: math.div(1, 3) * 100%;\n  text-align: center;\n  font-size: 14px;\n  height: 44px;\n  line-height: 36px;\n  transition: all 0.3s ease;\n  color: #8a8a8a;\n  cursor: pointer;\n  float: right;\n  border: solid 4px #fff;\n\n  &.selected, &:hover {\n    background-color: #f9f9f9;\n  }\n\n  &.selected {\n    font-size: 17px;\n    background-color: #f5f5f5;\n  }\n}\n\n.#{$prefix}addon-list {\n  width: 100%;\n  background-color: #ffffff;\n  position: absolute;\n  z-index: 2;\n  overflow: auto;\n  top: 0;\n  bottom: 52px;\n  border-bottom: solid 1px #eee;\n  direction: ltr;\n\n  &.can-close {\n    padding-top: 30px;\n  }\n}\n\n.#{$prefix}month-list {\n  padding-top: 15px;\n\n  .#{$prefix}addon-list-item {\n    height: 54px;\n    line-height: 46px;\n\n    &.selected, &:hover {\n    }\n  }\n}\n\n.#{$prefix}addon-list-item[disabled=true] {\n  opacity: 0.3;\n  cursor: default !important;\n  background-color: transparent !important;\n}\n\n.#{$prefix}close-addon {\n  position: absolute;\n  top: 4px;\n  left: 4px;\n  z-index: 2;\n  width: 30px;\n  height: 30px;\n  line-height: 30px;\n  color: #444;\n  font-family: sans-serif;\n  text-align: center;\n  cursor: pointer;\n  background-color: rgba(0, 0, 0, 0.1);\n}\n\n.#{$prefix}time {\n  user-select: none;\n  -moz-user-select: none;\n  -webkit-user-select: none;\n\n  .#{$prefix}time-h,\n  .#{$prefix}time-m {\n    position: relative;\n    margin-top: 70px;\n    float: left;\n    width: 50%;\n    height: 100%;\n    text-align: center;\n    color: #a2a2a2;\n    font-family: sans-serif;\n\n    .counter {\n      font-size: 90px;\n      height: 100px;\n      line-height: 100px;\n      overflow: hidden;\n      position: relative;\n      direction: ltr;\n      transition: opacity 0.3s ease-in-out;\n    }\n\n    .counter-item {\n      height: inherit;\n      width: 51px;\n      display: inline-block;\n      vertical-align: text-top;\n      position: relative;\n    }\n  }\n\n  .#{$prefix}time-h:after {\n    position: absolute;\n    top: 50%;\n    right: 0;\n    content: ':';\n    font-size: 70px;\n    transform: translate(50%, -50%);\n    transition: inherit;\n  }\n\n  .up-arrow-btn,\n  .down-arrow-btn {\n    display: block;\n    cursor: pointer;\n    outline: none;\n    height: 34px;\n\n    &:active svg path {\n    }\n  }\n\n  &.disabled .counter-item {\n    opacity: 0.5;\n  }\n}\n\n.#{$prefix}prev-step {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 30px;\n  height: 30px;\n  text-align: center;\n  padding: 9px;\n  cursor: pointer;\n\n  &:hover {\n    background-color: rgba(0, 0, 0, 0.2);\n  }\n}\n\n[data-type=\"time\"] {\n  .#{$prefix}time {\n    .#{$prefix}time-m,\n    .#{$prefix}time-h {\n      margin-top: 40px;\n    }\n  }\n}\n\n.#{$prefix}is-inline {\n  position: static;\n  background-color: transparent;\n  height: auto;\n  width: auto;\n  display: block;\n  margin-top: 2px;\n\n  .#{$prefix}container {\n    position: static;\n    transform: none;\n  }\n\n  .#{$prefix}content {\n    box-shadow: 0 0 1px -1px black;\n  }\n}\n\n.#{$prefix}no-footer {\n  .#{$prefix}addon-list {\n    bottom: 0;\n    border-bottom: none;\n  }\n}\n\n@media screen and (max-height: 460px) {\n  .#{$prefix}wrapper {\n    overflow: auto;\n    text-align: center;\n\n    .#{$prefix}container {\n      position: relative;\n      top: 0;\n      left: 0;\n      transform: none;\n      display: inline-block;\n      margin: 20px auto;\n    }\n  }\n}\n\n.#{$prefix}locales {\n  list-style-type: none;\n  padding: 0;\n  margin: 0;\n  position: absolute;\n  left: 14px;\n  top: 12px;\n  line-height: 24px;\n  text-align: center;\n  opacity: 0.7;\n\n  li {\n    cursor: pointer;\n\n    &.active {\n      display: none;\n    }\n  }\n}\n\n.#{$prefix}dir-ltr {\n  .#{$prefix}content,\n  .#{$prefix}actions,\n  .#{$prefix}addon-list-content {\n    direction: ltr;\n  }\n\n  .#{$prefix}content {\n    text-align: left;\n    font-family: sans-serif;\n  }\n  \n  .#{$prefix}actions {\n    text-align: center;\n    font-family: sans-serif;\n  }\n\n  .#{$prefix}month-label {\n    font-size: 90%\n  }\n\n  .#{$prefix}addon-list-item,\n  .#{$prefix}weekday,\n  .#{$prefix}day {\n    float: left;\n  }\n\n  .#{$prefix}locales {\n    left: auto;\n    right: 14px;\n  }\n}\n\n.#{$prefix}dir-rtl {\n  .#{$prefix}next,\n  .#{$prefix}prev {\n    transform: rotateY(180deg);\n  }\n\n  .#{$prefix}next {\n    float: left\n  }\n\n  .#{$prefix}prev {\n    float: right\n  }\n}\n"],"sourceRoot":""}]);
