___CSS_LOADER_EXPORT___.push([module.id, "\n.form-label[data-v-2902fbf4] {\n  font-weight: 500;\n  margin-bottom: 0.5rem;\n}\n.form-select[data-v-2902fbf4] {\n  margin-bottom: 1rem;\n}\n", "",{"version":3,"sources":["webpack://./src/components/examples/SimpleDate.vue"],"names":[],"mappings":";AAoKA;EACE,gBAAgB;EAChB,qBAAqB;AACvB;AACA;EACE,mBAAmB;AACrB","sourcesContent":["<template>\n  <div class=\"pt-5\">\n    <h1 class=\"text-center\" style=\"margin-bottom: 50px\">\n      VUE DATE TIME PICKER\n    </h1>\n    <h5 class=\"text-center mb-5\">\n      A vue plugin to select date and time\n    </h5>\n\n    <card title=\"Dynamic Calendar Configuration\">\n      <div class=\"mb-4\">\n        <div class=\"row\">\n          <div class=\"col-md-6\">\n            <label class=\"form-label\">Select Calendar Type:</label>\n            <select v-model=\"selectedCalendar\" class=\"form-select\">\n              <option value=\"gregory\">Gregorian</option>\n              <option value=\"jalali\">Jalali</option>\n              <option value=\"hijri\">Hijri</option>\n              <option value=\"japanese\">Japanese</option>\n              <option value=\"thai\">Thai</option>\n            </select>\n          </div>\n          <div class=\"col-md-6\">\n            <label class=\"form-label\">Select Locale:</label>\n            <select v-model=\"selectedLocale\" class=\"form-select\">\n              <option value=\"en\">English</option>\n              <option value=\"ja\">Japanese</option>\n              <option value=\"th\">Thai</option>\n              <option value=\"fa\">Persian</option>\n              <option value=\"fr\">French</option>\n              <option value=\"ar-sa\">Arabic</option>\n              <option value=\"ka\">Georgian</option>\n              <option value=\"ko\">Korean</option>\n              <option value=\"zh-cn\">Simplified Chinese</option>\n              <option value=\"zh-tw\">Traditional Chinese</option>\n            </select>\n          </div>\n          <div class=\"col-md-6\">\n            <label class=\"form-label\">First Day of Week:</label>\n            <select v-model.number=\"selectedDow\" class=\"form-select\">\n              <option :value=\"0\">Sunday (Default)</option>\n              <option :value=\"1\">Monday</option>\n              <option :value=\"2\">Tuesday</option>\n              <option :value=\"3\">Wednesday</option>\n              <option :value=\"4\">Thursday</option>\n              <option :value=\"5\">Friday</option>\n              <option :value=\"6\">Saturday</option>\n            </select>\n          </div>\n          <div class=\"col-md-6\">\n            <div class=\"form-check\">\n              <input\n                type=\"checkbox\"\n                class=\"form-check-input\"\n                id=\"useCustomDow\"\n                v-model=\"useCustomDow\"\n              />\n              <label class=\"form-check-label\" for=\"useCustomDow\">\n                Use Custom First Day of Week\n              </label>\n            </div>\n          </div>\n        </div>\n      </div>\n\n      <div class=\"mb-4\">\n        <h6>Current Configuration:</h6>\n        <p>Calendar: {{ selectedCalendar }} | Locale: {{ selectedLocale }} | First Day: {{ dowLabel }}</p>\n      </div>\n\n      <date-picker\n        v-model=\"dynamicDate\"\n        :locale=\"selectedLocale\"\n        :calendar=\"selectedCalendar\"\n        :type=\"'date'\"\n        :display-format=\"'YYYY-MM-DD'\"\n        :format=\"'YYYY-MM-DD'\"\n        :input-format=\"'YYYY-MM-DD'\"\n        :auto-submit=\"true\"\n        :placeholder=\"placeholder\"\n        :dow=\"selectedDow\"\n        :use-custom-dow=\"useCustomDow\"\n      />\n    </card>\n  </div>\n</template>\n\n<script>\nexport default {\n  name: 'SimpleDate',\n  data() {\n    return {\n      // Dynamic picker configuration\n      selectedCalendar: 'gregory',\n      selectedLocale: 'en',\n      selectedDow: 0,\n      useCustomDow: false,\n      dynamicDate: '',\n\n      // Static examples\n      date: '',\n      date1: '',\n      date2: '',\n      date3: '',\n      date4: '',\n      date5: '',\n      date6: '',\n      date7: '',\n      date8: '',\n      dateJalaliJa: '',\n      datetime: '',\n      time: '',\n      yearMonth: '',\n      year: '',\n      month: '',\n      week: '',\n      quarter: '',\n      day: '',\n      hour: '',\n      minute: '',\n      second: '',\n      millisecond: ''\n    }\n  },\n  computed: {\n    dowLabel() {\n      const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday']\n      return days[this.selectedDow]\n    },\n    displayFormat() {\n      const formats = {\n        ja: 'YYYYMD',\n        th: 'YYYY MMMM D',\n        'ar-sa': 'YYYY/MM/DD',\n        fa: 'YYYY/MM/DD',\n        fr: 'DD/MM/YYYY',\n        ka: 'DD/MM/YYYY',\n        ko: 'YYYY.MM.DD',\n        'zh-cn': 'YYYY/MM/DD',\n        'zh-tw': 'YYYY/MM/DD',\n        en: 'YYYY-MM-DD'\n      }\n      return formats[this.selectedLocale] || 'YYYY-MM-DD'\n    },\n    placeholder() {\n      const placeholders = {\n        ja: '',\n        th: '',\n        'ar-sa': ' ',\n        fa: '   ',\n        fr: 'Slectionner une date',\n        ka: ' ',\n        ko: ' ',\n        'zh-cn': '',\n        'zh-tw': '',\n        en: 'Select date'\n      }\n      return placeholders[this.selectedLocale] || 'Select date'\n    }\n  }\n}\n</script>\n\n<style scoped>\n.form-label {\n  font-weight: 500;\n  margin-bottom: 0.5rem;\n}\n.form-select {\n  margin-bottom: 1rem;\n}\n</style>\n"],"sourceRoot":""}]);
