//! dayjs locale configuration

export default {
  name: 'zh-cn',
  months: '___________'.split('_'),
  monthsShort: '1_2_3_4_5_6_7_8_9_10_11_12'.split('_'),
  weekdays: '______'.split('_'),
  weekdaysShort: '______'.split('_'),
  weekdaysMin: '______'.split('_'),
  longDateFormat: {
    LT: 'HH:mm',
    LTS: 'HH:mm:ss',
    L: 'YYYY/MM/DD',
    LL: 'YYYYMMDD',
    LLL: 'YYYYMMDD HH:mm',
    LLLL: 'YYYYMMDD dddd HH:mm'
  },
  calendar: {
    sameDay: '[] LT',
    nextDay: '[] LT',
    nextWeek: 'dddd LT',
    lastDay: '[] LT',
    lastWeek: 'dddd LT',
    sameElse: 'L'
  },
  relativeTime: {
    future: '%s',
    past: '%s',
    s: '',
    ss: '%d',
    m: '1',
    mm: '%d',
    h: '1',
    hh: '%d',
    d: '1',
    dd: '%d',
    M: '1',
    MM: '%d',
    y: '1',
    yy: '%d'
  },
  dayOfMonthOrdinalParse: /\d{1,2}(||)/,
  ordinal: function(number, period) {
    switch (period) {
      case 'd':
      case 'D':
      case 'DDD':
        return number + ''
      case 'M':
        return number + ''
      case 'w':
      case 'W':
        return number + ''
      default:
        return number
    }
  },
  week: {
    dow: 1, // Monday is the first day of the week
    doy: 4  // The week that contains Jan 4th is the first week of the year
  },
  formats: {
    LT: 'HH:mm',
    LTS: 'HH:mm:ss',
    L: 'YYYY/MM/DD',
    LL: 'YYYYMMDD',
    LLL: 'YYYYMMDD HH:mm',
    LLLL: 'YYYYMMDD dddd HH:mm'
  }
} 